@import url(https://fonts.googleapis.com/css2?family=Manrope&display=swap);
.root{
    background-color: #8c52ff11;
    padding: 30px;
    font-family: 'Manrope', sans-serif;    
}  
.root *{
    font-family: 'Manrope', sans-serif;
}
.flex-container{
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}
.footer-link{
    font-size: 1rem;
    text-decoration: underline;
    cursor: pointer;
}
.copy{
    text-align: center;
    font-size: 1rem;
    color: #00000080
}

/* Individual item */
.bm-item {
    display: inline-block;
  
    /* Our sidebar item styling */
    text-decoration: none;
    margin-bottom: 10px;
    color: black;
    /* transition: color 0.2s; */
  }
  
  /* Change color on hover */
  .bm-item:hover {
    color: #8c52ff !important;
  }
/*    */
  /* The rest copied directly from react-burger-menu docs */
  
  /* Position and sizing of burger button */
  .bm-burger-button {
    position: fixed;
    width: 20px;
    height: 20px;
    left: 20px;
    top: 15px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background:#8c52ff;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: white;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }
  @media only screen and (min-width: 1100px) {

    #sidenav-menu{
      display: none;
    }

    #sidenav-menu * {
      display: none;
    }
    .bm-burger-button{
      display: none;
    }
  }
  /* @media only screen and (max-width: 600) { */
    .article-description{
      display: none !important;
    }
  /* } */

.email-form{
    padding: 5px 0px;
    border: 1px solid #8c52ff;
    background: #8c52ff20;
    border-radius: 5px;
    margin-bottom: 0px;
    text-align: center;
    position: relative;
}
.email-form h2{
    margin: 0;
    margin-bottom: 10px;
    color: #8c52ff;
}
.email-form input{
    display: inline-block;
    width: 300px;
    text-align: center;
    border: 2px solid #8c52ff;
    border-radius: 5px;
    margin-bottom: 10px;
    padding: 10px;
}
.email-form button{
    cursor: pointer;
}
#close-form{
    border: 2px solid #8c52ff;
    background: #8c52ff;
    color: #FFFFFF;
    border-radius: 100px;
    padding: 3px 7px;
    /* margin-left: 7px;    */
    position: absolute;
    top: 7px;
    left: 7px;
}   
#subscribe-button{
    cursor: pointer;
    display: inline-block;  
    text-align: center;
    width: 300px;
    padding: 10px;
    border: 2px solid #8c52ff;
    background: #8c52ff;
    color: #FFFFFF;
    border-radius: 100px;
    margin-bottom: 10px;
}
#subscribe-button *{
    margin: 0px;
    padding: 0px;
}
.warning-message{
    font-size: 1rem;
}
.subscribe-success{
    background: #00FF0020;
    border: 2px solid #00FF00;
    border-radius: 5px;
    margin-bottom: 0;
    padding: 10px;
}

/* PrismJS 1.20.0
https://prismjs.com/download.html#themes=prism-okaidia&languages=markup+css+clike+javascript */
/**
 * okaidia theme for JavaScript, CSS and HTML
 * Loosely based on Monokai textmate theme by http://www.monokai.nl/
 * @author ocodia
 */

code[class*="language-"],
pre[class*="language-"] {
	color: #f8f8f2;
	background: none;
	text-shadow: 0 1px rgba(0, 0, 0, 0.3);
	font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	font-size: 1em;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;

	-moz-tab-size: 4;
	tab-size: 4;

	-webkit-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
}

/* Code blocks */
pre[class*="language-"] {
	padding: 1em;
	margin: .5em 0;
	overflow: auto;
	border-radius: 0.3em;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	background: #272822;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: .3em;
	white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
	color: slategray;
}

.token.punctuation {
	color: #f8f8f2;
}

.token.namespace {
	opacity: .7;
}

.token.property,
.token.tag,
.token.constant,
.token.symbol,
.token.deleted {
	color: #f92672;
}

.token.boolean,
.token.number {
	color: #ae81ff;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
	color: #a6e22e;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable {
	color: #f8f8f2;
}

.token.atrule,
.token.attr-value,
.token.function,
.token.class-name {
	color: #e6db74;
}

.token.keyword {
	color: #66d9ef;
}

.token.regex,
.token.important {
	color: #fd971f;
}

.token.important,
.token.bold {
	font-weight: bold;
}
.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}


